  }
  .item-infoo h4 {
    margin-bottom: 0.5rem;
  }
  .price {
    color: #c59d5f;
    color: var(--clr-gold);
  }
  .item-text {
    margin-bottom: 0;
    padding-top: 1rem;
  }
  
  @media screen and (min-width: 768px) {
    .menu-item {
      grid-template-columns: 225px 1fr;
      grid-gap: 0 1.25rem;
      gap: 0 1.25rem;
      max-width: 40rem;
    }
    .photo {
      height: 175px;
    }
  }
  @media screen and (min-width: 1200px) {
    .section_center {
      width: 95vw;
      grid-template-columns: 1fr 1fr;
    }
    .photo {
