/*
=============== 
Slider
===============
*/
.title {
  margin-top: 50px;
  text-align: center;
  margin-bottom: 2rem;
}
.title h2 {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
}
.title span {
  font-size: 0.85em;
  color: hsl(21, 62%, 45%);
  color: var(--clr-primary-5);
  margin-right: 1rem;
  font-weight: 700;
}
.section-center {
  margin: 0 auto;
  margin-top: 4rem;
  width: 80vw;
  /* have to have a height */
  height: 450px;
  max-width: 800px;
  text-align: center;
  position: relative;
  display: flex;
  overflow: hidden;
}
.person-img {
  border-radius: 50%;
  margin-bottom: 1rem;
  width: 150px;
  height: 150px;
  object-fit: cover;
  border: 4px solid hsl(210, 31%, 80%);
  border: 4px solid var(--clr-grey-8);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  box-shadow: var(--dark-shadow);
}
article h4 {
  text-transform: uppercase;
  color: hsl(21, 62%, 45%);
  color: var(--clr-primary-5);
  margin-bottom: 0.25rem;
}
.title {
  text-transform: capitalize;
  margin-bottom: 0.75rem;
  color: hsl(209, 34%, 30%);
  color: var(--clr-grey-3);
}
.text {
  max-width: 35em;
  margin: 0 auto;
  margin-top: 2rem;
  line-height: 2;
  color: hsl(210, 22%, 49%);
  color: var(--clr-grey-5);
}
.icon {
  font-size: 3rem;
  margin-top: 1rem;
  color: hsl(21, 62%, 45%);
  color: var(--clr-primary-5);
}
.prev,
.next {
  position: absolute;
  top: 200px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background: hsl(210, 22%, 49%);
  background: var(--clr-grey-5);
  color: #fff;
  color: var(--clr-white);
  width: 1.25rem;
  height: 1.25rem;
  display: grid;
  place-items: center;
  border-color: transparent;
  font-size: 1rem;
  border-radius: 0.25rem;
  border-radius: var(--radius);
  cursor: pointer;
  transition: all 0.3s linear;
  transition: var(--transition);
}
.prev:hover,
.next:hover {
  background: hsl(21, 62%, 45%);
  background: var(--clr-primary-5);
}
.prev {
  left: 0;
}
.next {
  right: 0;
}
@media (min-width: 800px) {
  .text {
    max-width: 45em;
  }
  .prev,
  .next {
    width: 2rem;
    height: 2rem;
    font-size: 1.5rem;
  }
}
article {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 0.3s linear;
  transition: var(--transition);
}
article.activeSlide {
  opacity: 1;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
article.lastSlide {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
article.nextSlide {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

